<template>
  <div class="big-box">
    <div class="container">
      <div class="container-header">
        <div class="container-header-left">
          <span class="span-header">全部可视化</span>
          <span class="already-created">已创建</span>
          <span class="span-data">{{createdNum}}</span>
          <span class="can-created">可创建</span>
          <span class="span-data">{{canCreatedNum}}</span>
        </div>
        <div class="search-box">
          <input type="text" class="search-input" v-model.trim="searchVal" placeholder="搜索可视化" />
          <span class="biFont icon-search"></span>
        </div>
      </div>
      <!-- :wrapStyle="wrapStyle"> -->
      <div class="content-body">
        <easy-scroll :barOption="scrollOpt">
          <div class="content-body-wrapper">
            <div @click="throttleCreateVisual(['新建可视化'])" class="add-new" v-show="isShowBtn">
              <!-- v-show="isShowBtn"> -->
              <span class="biFont icon-add"></span>
              <div>新建可视化</div>
            </div>
            <div v-for="(item,index) in searchscreenList" :key="item.id">
              <div class="content" :class="{actived:item.show||item.isShowEdit}">
                <div class="editMask" :class="{transparent:item.show}">
                  <div class="btn-wrapper allCenter">
                    <span class="biFont icon-edit2" @click="toScreen(item)">
                      <span class="tips aCenter">编辑</span>
                    </span>
                    <span class="biFont icon-preview3" @click="preview(item.id)">
                      <span class="tips aCenter">预览</span>
                    </span>
                  </div>
                </div>
                <div class="optionsDiv">
                  <span
                    class="biFont icon-setting item-options"
                    :class="{'add-class':item.show}"
                    @mouseup.stop="addMoreDiv(item)"
                  ></span>
                  <div
                    class="content-header-more"
                    @mousedown.stop
                    v-click-outside="{fn:hideMoreMenu}"
                    v-show="item.show"
                  >
                    <ul>
                      <li
                        v-for="(iconItem,i) in itemIconList"
                        :key="iconItem.id"
                        @click="choiceItem(index,i,item)"
                      >
                        <span class="biFont" :class="`icon-${iconItem.iconName}`"></span>
                        {{iconItem.name}}
                      </li>
                    </ul>
                  </div>
                </div>
                <img class="content-bg" :src="item.screenShot?item.screenShot:defaultBg" />
                <div class="content-footer">
                  <div class="title">
                    <div class="rename-box">
                      <div class="input-box" :title="item.name">
                        <span class="hidden input-hidden" ref="widthCalc">{{item.name}}</span>
                        <input
                          ref="editInput"
                          :id="index"
                          type="text"
                          spellcheck="false"
                          class="edit-input"
                          autocomplete="off"
                          v-model.trim="item.name"
                          :class="{readonly:!item.isShowEdit}"
                          @keydown.space.prevent="filter"
                          @keyup.enter="keyBlur($event)"
                          @input="widthChange(item,index,$event)"
                          @blur="inputBlur(index,item)"
                        />
                      </div>
                      <!-- <label @click="showInput(item,index)" class="editLabel vCenter" :key="index">
                        <span
                          class="biFont icon-edit icon-class-edit vCenter"
                          :class="{visible:item.show}"
                          @mouseenter="addHoverTips(index,1)"
                          @mouseleave="removeHoverTips"
                        ></span>
                        <span class="tips" v-show="index == isActiveTips">重命名</span>
                      </label>-->
                    </div>
                  </div>
                  <div class="projectInfo">
                    <!-- <span class="content-footer-title">{{item.num}}</span>
                    <span class="biFont icon-projectNum projectNum"></span>
                    <span class="content-footer-date">{{item.time}}</span>-->
                    {{item.time}}
                  </div>
                </div>
              </div>
            </div>
            <Modal ref="modal" :options="promptData" v-if="isShowModal" @setTips="nameCheck"></Modal>
          </div>
        </easy-scroll>
      </div>
    </div>
    <PreViewModal data-class="unableDeactive"></PreViewModal>
  </div>
</template>

<script>
let defaultBg =
  "data:image/png;base64,";
import Modal from "@/components/Prompt/Modal";
import PreViewModal from "./Project/components/Footer/PreViewModal";
import { throttle } from "@/utils/utilsMethods";
import { mapGetters, mapMutations } from "vuex";
import { getQueryString, getCookie } from "@/utils/utilsMethods";
export default {
  name: "Visualization",
  components: {
    Modal,
    PreViewModal
  },
  data() {
    return {
      throttleCreateVisual: () => {},
      isShowBtn: true,
      isShowMore: false,
      isShowInput: false,
      newPlaceholder:'',
      lastChar: "",
      searchVal: "",
      createdNum: 10,
      canCreatedNum: 7,
      isActiveEdit: -1,
      isActiveTips: -1,
      isMoreIndex: -1,
      OperandIndex: -1,
      rename: "",
      isShowModal: false,
      Operand: {},
      defaultBg,
      token: getCookie("token"),
      screenShotList: [],
      itemIconList: [
        // {
        //   id: 1,
        //   name: "复制",
        //   iconName: "copy"
        // },
        // {
        //   id: 2,
        //   name: "导出",
        //   iconName: "export"
        // },
        {
          id: 0,
          name: "重命名",
          iconName: "edit"
        },
        {
          id: 1,
          name: "分享",
          iconName: "share"
        },
        {
          id: 2,
          name: "删除",
          iconName: "delete"
        }
      ],
      screenList: [],
      promptData: {},
      createScreenData: {
        title: "新建可视化",
        icon: "biFont icon-builtNew",
        describe: "",
        inputType: "text",
        placeholder: "请输入可视化名称",
        name: [""],
        boxWidth: 29.3,
        inputWidth: 20.8,
        tipsMessage: "",
        isVisual: true,
        btn: [
          {
            text: "确定",
            callback: () => {
              this.nameCheck(this.newPlaceholder,true);
            }
          },
          {
            text: "取消",
            callback: () => {
              this.isShowModal = false;
              this.createScreenData.tipsMessage = "";
            }
          }
        ],
        enterKeyEventIndex: 0,
        escKeyEventIndex: 1
      },
      deleteScreenData: {
        title: "提示!",
        describe: "是否删除该项目？",
        inputType: "text",
        placeholder: "",
        name: [],
        btn: [
          {
            text: "确认",
            throttle: true,
            callback: () => {
              this.deleteScreen(this.Operand);
            }
          },
          {
            text: "取消",
            callback: () => {
              this.isShowModal = false;
            }
          }
        ],
        enterKeyEventIndex: 0,
        escKeyEventIndex: 1
      },
      scrollOpt: {
        barColor: "#343a47", //滚动条颜色
        barWidth: 6,
        railColor: "#000000", //导轨颜色
        barMarginRight: 6, //垂直滚动条距离整个容器右侧距离单位（px）
        barOpacityMin: 0.3 //滚动条非激活状态下的透明度
      }
    };
  },
  methods: {
    //鼠标移上添加样式
    addHover(index) {
      this.isActiveEdit = index;
    },
    removeHover() {
      this.isActiveEdit = null;
    },
    addHoverTips(index) {
      this.isActiveTips = index;
    },
    removeHoverTips() {
      this.isActiveTips = -1;
    },
    addMoreDiv(item) {
      this.hideMoreMenu();
      item.show = !item.show;
    },
    hideMoreMenu(item) {
      if (item) {
        this.$set(item, "show", false);
        return;
      }
      this.screenList.forEach(ele => {
        this.$set(ele, "show", false);
      });
    },
    toScreen(item) {
      localStorage.setItem("fatherProjectId", item.id);
      localStorage.setItem("fatherProjectName", item.name);
      let a = localStorage.getItem("fatherProjectId");
      this.$router.push({
        name: "screen"
      });
    },
    //切换input与span
    showInput(item, index) {
      this.rename = item.name;
      let len = this.rename.length;
      this.$set(item, "isShowEdit", true);
      let editInput = this.$refs.editInput.find(ele => ele.id == index);
      editInput.focus();
      editInput.selectionStart = len;
      editInput.selectionEnd = len;
      let i = this.getEditInputId(index);
      this.$refs.editInput[i].select();
    },
    //找到refs对应的id的Dom
    getEditInputId(index) {
      let arr = this.$refs.editInput;
      for (let i = 0; i < arr.length; i++) {
        if (arr[i].id == index) {
          return i;
        }
      }
    },
    inputBlur(index, item) {
      // let editInput = this.$refs.editInput.find(ele => ele.id == index);
      let name = item.name;
      // this.$set(item, 'name', name.replace(/\s+/g, ''));
      this.$set(
        item,
        "name",
        name.replace(/[^\u4e00-\u9fa5w|a-zA-Z0-9\s|.()]/g, "")
      );
      // editInput.blur();
      item.isShowEdit = false;
      this.Operand = item;
      this.lastChar = "";
      console.log(this.rename, item.name, this.rename == item.name);
      if (this.rename && item.name && this.rename != item.name) {
        let tips = this.nameRules([item.name], index);
        if (tips !== "命名成功") {
          this.tips.show([tips]);
          this.$set(this.screenList[index], "name", this.rename);
          return;
        }
        this.renameScreen();
        return false;
      }
      if (!item.name) {
        this.$set(this.screenList[index], "name", this.rename);
        this.tips.show(["可视化名称不能为空"]);
      }
    },
    keyBlur(e) {
      const target = e.target || e.srcElement;
      target.blur();
    },
    widthChange(item, index, e) {
      if (!e.isComposing) {
        this.lastChar = "";
      }
      if (this.lastChar.lastIndexOf(e.data) > -1 && e.isComposing) {
        let len = item.name.length;
        let name = item.name.substr(0, len - 2);
        this.$set(item, "name", name);
      }
      // if (!e.data && e.isComposing) {
      // 	let len = e.data.length;
      // 	let name = item.name.substr(0, len - 2);
      // 	this.$set(item, 'name', name);
      // }
      if (e.data && e.isComposing) {
        let len = e.data.length;
        let char = e.data[len - 1];
        let name = item.name + char;
        this.$set(item, "name", name);
        this.lastChar = e.data;
      }
    },
    /**
     * 以下方法都是调接口的
     * 新建可视化
     * 初始化项目
     * 删除可视化
     * 重命名
     */
    createVisual(data) {
      //新建可视化
      this.isShowModal = true;
      this.promptData = this.createScreenData;
    },
    initProject() {
      //可视化初始化
      let vm = this;
      vm.screenList = [];
      let token = vm.token;
      vm.$get("api/project", { token })
        .then(res => {
          if (res.code == "000000") {
            let screenList = res.data;
            screenList.forEach(e => {
              vm.$set(e, "show", false);
              vm.$set(e, "bgUrl", vm.defaultBg);
              vm.$set(e, "isShowEdit", false);
            });
            vm.screenList = screenList;
            console.log(vm.screenList);

            // 测试
            // vm.screenList.forEach(item=>{
            //   vm.screenShotList.push(item.screenShotSn);
            // })
            //  vm.initSshot();
          }
        })
        .catch(error => {
          console.log(error);
        });
      vm.createdNum = vm.screenList.length;
      vm.canCreatedNum = 10 - vm.createdNum;
    },
    // 测试接口
    // initSshot(){
    //   let vm = this;
    //   let screen_shot = JSON.stringify(vm.screenShotList);
    //   const obj = {
    //     token: vm.token,
    //     screen_shot: screen_shot
    //   }
    //   vm.$post("api/screenShot", obj)
    //     .then(res => {
    //       if (res.code == "000000") {
    //         let imgsrc = res.data
    //          vm.searchscreenList.forEach((item,i)=>{
    //           //  item.screenShot = imgsrc[i].screen_shot_image_url
    //            vm.$set(item,'screenShot',imgsrc[i].screen_shot_image_url)
    //          })
    //         console.log(vm.searchscreenList);
    //       }
    //     })
    //     .catch(error => {
    //       console.log(error)
    //     });
    //
    // },
    choiceItem(index, i, item) {
      // console.log(index, i, item);
      this.Operand = item;
      this.OperandIndex = index;
      let changeStatus = {
        0: () => {
          console.log(index, i, item);
          this.showInput(item, index);
        },
        1: () => {
          this.$get('api/project', {
            projectId: this.screenList[index].id
          }).then(res => {
            sessionStorage.setItem("projectId",res.data[0].id);
            this.$store.commit("canvasPlace/setStateProperty", {
              key: "shareType",
              value: 1
            });
          })
        },
        2: () => {
          this.promptData = this.deleteScreenData;
          this.isShowModal = true;
        }
      };
      changeStatus[i] && changeStatus[i]();
      this.hideMoreMenu(item);
    },
    deleteScreen(Operand) {
      let vm = this;
      let delObject = {
        id: Operand.id,
        type: "father"
      };

      vm.$del("/api/project", delObject)
        .then(res => {
          if (res.code == "000000") {
            vm.screenList.splice(vm.OperandIndex, 1);
            vm.OperandIndex = -1;
            vm.isShowModal = false;
            vm.tips.show(["项目删除成功", true]);
          } else {
            vm.tips.show([res.err_msg.split(":")[1]]);
          }
        })
        .catch(error => {
          vm.tips.show(["项目删除失败"]);
          console.error(error);
        });
    },
    renameScreen() {
      let vm = this;
      let obj = {
        id: vm.Operand.id,
        name: vm.Operand.name
      };
      vm.$put("api/project", obj)
        .then(res => {
          // console.log('one-put', res);
          if (res.code == "000000") {
            vm.isActiveEdit = -1;
            vm.tips.show(["可视化重命名成功", true]);
            vm.rename = "";
          } else {
            vm.tips.show([res.err_msg.split(":")[1]]);
          }
        })
        .catch(error => {
          vm.tips.show(["可视化重命名失败"]);
        });
    },
    newVisual() {
      this.isShowModal = true;
      this.promptData = this.createScreenData;
    },
    /**
     * 预览
     */
    preview(curFatherId) {
      const vm = this;
      async function doIt(curFatherId) {
        const projectSn = await getFirstProjectSn(curFatherId);
        const previewCurProject = await getPreviewCurProject(projectSn);
        if (previewCurProject) {
          const address = `${window.location.host}/previewPages?share_sn=${previewCurProject.share_sn}&password=${previewCurProject.password}`;
          window.open(window.location.protocol + "//" + address, "_blank");
        }
      }

      function getFirstProjectSn(curFatherId) {
        return new Promise((resolve, reject) => {
          vm.$get("api/project", { projectId: curFatherId })
            .then(res => {
              // console.log("api/project", res);
              if (res.code == "000000") {
                resolve(res.data[0]["id"]);
              } else {
                vm.tips.show([res.err_msg]);
                reject(res.err_msg);
              }
            })
            .catch(error => {
              console.error(error);
            });
        });
      }

      function getPreviewCurProject(curProjectSn) {
        return new Promise((resolve, reject) => {
          const shareObj = {
            project_sn: curProjectSn,
            share_type: 1,
            is_pwd: 0
          };
          vm.$get("api/shareUnique", shareObj)
            .then(res => {
              if (res.code == "000000") {
                resolve(res.data);
              } else {
                reject(res.err_msg);
              }
            })
            .catch(error => {
              vm.tips.show(["获取预览链接失败"]);
              console.error(error);
            });
        });
      }

      doIt(curFatherId);
    },
    //创建可视化弹窗
    visualCreate(placeholder) {
      let vm = this;
      if (!placeholder) {
      }
      const obj = {
        type: "father",
        name: placeholder[0],
        dashboardWidth: 1920,
        dashboardHeight: 1020,
        zoomLevel: 0.7
      };
      vm.screenList.forEach((e, i) => {
        if (e.name == obj.name) {
          obj.name = obj.name + "(" + (i + 1) + ")";
        }
      });
      console.log(obj);
      vm.$post("api/project", obj)
        .then(res => {
          if (res.code == "000000") {
            vm.isShowModal = false;
            let data = res.data;
            vm.$set(data, "screenShot", vm.defaultBg);
            vm.screenList.unshift(data);
            vm.tips.show(["可视化创建成功", true]);
            //直接进入新创建的BI工具
            this.toScreen(data)
          } else {
            vm.tips.show([res.err_msg.split(":")[1]]);
          }
        })
        .catch(error => {
          vm.tips.show(["可视化创建失败"]);
        });
    },
    nameCheck(placeholder,isClickBtn) {
      this.newPlaceholder = placeholder
      this.createScreenData.tipsMessage = "";
      let tips
      if (typeof placeholder === 'string') {
        placeholder = Array(placeholder)
      }
      tips = this.nameRules(placeholder);
      if (tips != "命名成功") {
        this.createScreenData.tipsMessage = tips;
      } else {
        //isClickBtn判断是否是点击‘确定’按钮
        if(!isClickBtn) return
        this.visualCreate(placeholder);
        this.createScreenData.tipsMessage = "";
      }
    },
    nameRules(arr, index) {
      //新建可视化窗口命名规则
      let str = arr[0];
      let tips = "";
      let allName = [];
      this.screenList.forEach(obj => {
        allName.push(obj.name);
      });
      if (typeof index !== "undefined") {
        allName.splice(index, 1);
      }
      let already;
      if (str) {
        already = allName.includes(str.trim());
      }
      tips =
        !str || str.trim() == ""
          ? "该名称不能为空"
          : already
          ? "该名称已重复"
          : this.getNameLen(str) > 29
          ? "已达最大长度"
          : "命名成功";
      return tips;
    },
    getNameLen(str) {
      //判断命名的长度中文2字符，英文1字符
      if (str == null) return 0;
      if (typeof str != "string") {
        str += "";
      }
      return str.replace(/[^\x00-\xff]/g, "01").length;
    },
    //清空新建可视化弹窗数据
    clearVisualPopup() {}
  },
  beforeMount() {
    this.throttleCreateVisual = throttle(this.createVisual, 1000);
  },
  mounted() {
    this.initProject();
  },
  watch: {
    creatVisual() {
      // 语音创建可视化
      if (this.creatVisual === "await") return;
      this.throttleCreateVisual(["新建可视化"]);
    },
    computedCount(newVal) {
      this.createdNum = newVal;
      this.canCreatedNum = 10 - this.createdNum;
      if (this.canCreatedNum === 0) {
        this.isShowBtn = false;
      } else {
        this.isShowBtn = true;
      }
    }
  },
  computed: {
    ...mapGetters("voiceCommand", {
      creatVisual: "getVisual" // 语音创建命令
    }),
    computedCount() {
      return this.screenList.length || 0;
    },
    searchscreenList() {
      let vm = this;
      let returnscreenList = [];
      vm.screenList.forEach(e => {
        if (e.name.search(vm.searchVal) !== -1) {
          returnscreenList.push(e);
        }
      });
      if (returnscreenList.length > 0) {
        return returnscreenList;
      } else {
        return vm.screenList;
      }
    },
    vw() {
      return this.$store.state.main.vw;
    },
    vh() {
      return this.$store.state.main.vh;
    },
    reactiveScrollOpt() {
      //滚动条响应式参数
      return {
        ...this.scrollOpt,
        barWidth: this.vw * 0.4
      };
    }
  }
};
</script>

<style scoped>
.big-box {
  width: 100%;
  height: 100%;
  background-color: #11151f;
  user-select: none;
}

.container {
  height: 100%;
}

.container-header {
  display: flex;
  padding: 3.7vh 5vw 0;

  justify-content: space-between;
}

.container-header-left {
  display: flex;
  flex-grow: row wrap;
  align-items: center;
  height: 2.8vh;
}

.container-header .search-input {
  width: 19.2vw;
  height: 2.8vh;
  background-color: #283247;
  padding-left: 0.4vw;
  border: solid 0.0625vw transparent;
  font-size: 0.8vw;
  color: #ffffff;
}

.container-header input::-webkit-input-placeholder {
  width: 4.4vw;
  font-size: 0.8vw;
  color: #798194;
}

.content-header .rename-box {
  display: inline-block;
  width: auto;
  height: 100%;
  position: relative;
}

.content-header .editLabel {
  position: absolute;
  width: 2vw;
  height: 1vw;
  line-height: 1vw;
  right: -2.5vw;
}

.icon-class-edit {
  visibility: hidden;
  position: absolute;
  cursor: pointer;
  font-size: 0.5vw;
  color: #28b3fc;
  text-align: center;
}

.editLabel .tips {
  position: absolute;
  font-size: 0.6vw;
  position: absolute;
  color: #ffffff;
  width: 2.5vw;
  height: 1.1vw;
  line-height: 1.1vw;
  top: 1.3vw;
  right: 0.4vw;
}

.content-header .search-input {
  height: 1vw;
  width: 6vw;
  font-size: 0.8vw;
  color: #ffffff;
  background-color: #2f3952;
  padding-top: 0.5vw;
  padding-left: 0.2vw;
  padding-bottom: 0.5vw;
}

.content-header-title {
  height: 0.8vw;
  font-size: 0.8vw;
  color: #ffffff;
  line-height: 0.8vw;
  text-align: center;
}

.content-span {
  display: flex;
  align-items: center;
  justify-content: center;
}

.content-header-more {
  position: absolute;
  top: 2.1vw;
  left: -1.2vw;
  width: 3.6vw;
  height: auto;
  z-index: 3;
  background-color: #252f40;
  /* box-shadow: 0vw 0.1vw 0.2vw 0vw rgba(0, 0, 0, 0.3); */
  box-shadow: 0 0 1.526vw 0 rgba(0, 0, 0, 0.9);
